.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.conteiner {
  max-width: 375px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  flex: 1;
}

@media screen and (min-width: 768px) {
  .conteiner {
    max-width: 768px;
  }
}

@media screen and (min-width: 1342px) {
  .conteiner {
    max-width: 1342px;
    padding-top: 40px;
    padding-bottom: 20px;
  }
}
